/* === COLORS & FONTS === */
:root {
  --bg-color-primary: #212121;
  --bg-color-secondary: #3e3e3e;
  --bg-color-dark-light: #828282;
  --accent-color-primary: #db7100;
  --accent-color-secondary: #ffae57;
  --accent-color-white: #ffffff;
  --accent-color-black: #212121; 
  --font-color-primary: #ffffff;
  --font-color-secondary: #212121;
}

/* === RESET === */
/* A very basic reset that sets margins and padding to 0 and */
/* box-sizing to border-box. The html page is set to the primary */
/* dark background color and the font to the light font color */

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html {
  background-color: var(--bg-color-primary);
}

h1, h2, h3, p {
  color: var(--font-color-primary);
}

.App {
  display: flex;
  transition: all 0.3s ease; /* Smooth transitions */
}

/* === CLASS ELEMENTS === */
/* The main class elements are  */

.main-page {
  flex: 1;
  display: flex;
  align-items: start;
  justify-content: center;
  padding-top: 5rem;
}

button {
  width: 7rem; 
  height: 2.5rem; 
  background-color: var(--accent-color-primary); 
  color: var(--font-color-primary); 
  font-size: 1rem; 
  font-weight: bold; 
  border: 1px solid var(--accent-color-white); 
  border-radius: 0.5rem; 
  cursor: pointer; 
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); 
  transition: background-color 0.3s, transform 0.2s;
}

/* === MAIN PAGE === */
/* The basic structure of the page contains a header and a hero with */
/* a navigation panel on the side. The header and hero elements */
/* are wrapped in a div element called "main-wrap" */

.main-wrap {
  flex-grow: 1;
  height: 100vh;
  display: flex;
  flex-direction: column;
  transition: margin-left 0.3s ease; /* Smooth transition for margin */
}

.App.expanded .main-wrap {
  margin-left: 1rem; /* Sidebar expanded */
}

.App.collapsed .main-wrap {
  margin-left: 1rem; /* Sidebar collapsed */
}

.header {
  background-color: var(--bg-color-primary);
  color: white;
  margin-left: 0.2rem;
  padding: 1.8rem 0 1rem 4rem;
  position: relative;
}

.header::after {
  content: ''; 
  display: block; 
  width: 5rem; 
  height: 3px; 
  background-color: var(--accent-color-primary); 
  position: absolute; 
  bottom: 0; 
  left: 4rem; 
}

.hero {
  background-color: var(--bg-color-primary);
  display: flex;
  flex-grow: 1;
  padding: 1.8rem 4rem 1rem 4rem; 
  width: 100%;
}

/* === SIDE NAVIGATION PANEL === */
/* The side navigation panel is a nav element called "sidenav"  */
/* The nav element has child element called "nav-wrapper" which is a div */
/* The child contains all logic for the top, middle and bottom of the navigation panel */

.sidenav {
  height: 100vh;
  width: 15rem;
  display: flex;
  flex-shrink: 0;
  flex-direction: column;
  padding: 1rem 0.5rem 1rem 0.5rem;
  background: var(--bg-color-primary);
  color: var(--font-color-primary);
  box-shadow: 0 0 1px rgba(0, 0, 0, 0.1);
  transition: width 0.3s ease; /* Smooth transition for width */
}

.sidenav.collapsed {
  width: 6.5rem; 
}

.nav-wrapper {
  background-color: var(--bg-color-secondary);
  margin: 0 0.8rem;
  padding: 1rem 1rem 1rem 1rem;
  border-radius: 10px;
  height: 100%;
  min-height: 25rem;
  display: flex;
  flex-direction: column;
  border: 1px solid var(--accent-color-primary);
}

.sidenav-top {
  display: flex;
  align-items: center;
  /* padding-left: 1rem; */
}

.sidenav-top img {
  height: 18px;
  margin-left: 0.5rem;
  transition: opacity 0.3s ease;
}

.sidenav.collapsed .sidenav-top img {
  opacity: 0;
}

.sidenav-middle {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  padding-top: 4rem;
  /* padding-left: 1rem; */
  flex-grow: 1;
  min-height: 10px;
}

.sidenav-bottom {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  padding: 2rem 0 1rem 0rem; 
  min-height: 17rem;
}

.navlist {
  list-style: none;
  display: grid;
  gap: 1.8rem;
}

.navlist .navlink {
  display: flex;
  gap: 0.5rem;
  align-items: center;
  text-decoration: none;
  color: white;
  transition: opacity 0.3s ease;
}

.navlist .navlink span {
  transition: opacity 0.3s ease;
}

.sidenav.collapsed .navlink span {
  opacity: 0;
  pointer-events: none; /* Disable text interaction when collapsed */
}


.icon-container {
  display: flex; 
  align-items: center;
}

/* === REGISTER PAGE === */
/* Each page is a child of the "hero" element (see "MAIN PAGE" above) */

.register-container {
  display: flex;
  flex-grow: 1;
  max-width: 41rem;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: var(--bg-color-secondary);
  border: 1px solid var(--accent-color-primary);
  border-radius: 10px;
}

.register-area {
  display: grid;
  place-items: center;
  gap: 2rem;
  padding: 3rem 0rem 3rem 0rem;
}

.intro-text {
  display: grid;
  place-items: center;
  gap: 0.8rem;
}

.register-buttons {
  display: flex;
  gap: 1rem;
}

.register-buttons button {
  width: 10rem;
}

.register-buttons button:hover {
  background-color: #a55500; 
  transform: translateY(-0.5px); 
}

.register-buttons button:active {
  background-color: #8b4800; 
}

.register-select {
  height: 2.5rem;
  width: 21rem;
  text-align: center;
  border: 2px solid var(--accent-color-primary);
  background-color: var(--accent-color-white);
  color: var(--bg-color-primary);
  border-radius: 0.5rem;
  padding: 0.2rem;
  transition: border-color 0.3s ease, background-color 0.3s ease;
}

/* === LOGIN PAGE === */
/* Each page is a child of the "hero" element (see "MAIN PAGE" above) */

.login-container {
  display: flex;
  flex-grow: 1;
  max-width: 41rem;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: var(--bg-color-secondary);
  border: 1px solid var(--accent-color-primary);
  border-radius: 10px;
}

.login-area {
  display: grid;
  place-items: center;
  gap: 2rem;
  padding: 3rem 0rem 3rem 0rem;
}

.input-container {
  display: grid;
  place-items: center;
  gap: 1rem;
}

.login-item {
  display: grid;
  gap: 0.3em;
}

.login-item p {
  font-size: 0.9rem;
  font-weight: bold;
}

.login-item input {
  height: 2.5rem;
  width: 21rem;
  padding: 0.5rem;
  border: 2px solid var(--accent-color-primary);
  background-color: var(--accent-color-white);
  color: var(--bg-color-secondary);
  border-radius: 0.5rem;
}

.login-item input::placeholder {
  color: var(--bg-color-secondary);
}

/* === DASHBOARD PAGE === */
/* Each page is a child of the "hero" element (see "MAIN PAGE" above) */

.dashboard-container {
  display: flex;
  flex: 1;
  margin: 0 5rem;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: var(--bg-color-secondary);
  border: 1px solid var(--accent-color-primary);
  border-radius: 10px;
  padding: 3rem 5rem 3rem 5rem;
  height: 80%;
  max-height: 431.523px;
  gap: 1.5rem;
}

.dashboard-area {
  display: flex;
  flex-direction: column;
  place-items: center;
  gap: 2rem;
  width: 100%;
  min-height: 100%;
  max-height: 100%;
  overflow-y: auto;
  position: relative;
  border: 1px solid white;
}

.dashboard-table {
  width: 100%;
  height: auto;
  text-align: left;
  color: var(--font-color-primary);
  border-collapse: collapse; 
}

.dashboard-table thead {
  position: sticky;
  top: 0;
  background-color: var(--bg-color-secondary);
  outline: 1px solid var(--accent-color-white);
  box-shadow: 0 2px 2px rgba(0, 0, 0, 0.1);
}

.dashboard-table th {
  padding: 8px;
  background-color: #a55500;
  text-align: left;
}

.dashboard-table td, .dashboard-table th {
  text-indent: 20px; 
  padding: 8px;
}

.dashboard-table tbody tr {
  height: 40px;
  border-bottom: 1px solid var(--accent-color-white);
}

.dashboard-table tbody tr:hover {
  background-color: var(--accent-color-secondary);
  color: var(--font-color-secondary);
}

/* Status Indicators */
.green-status, .red-status {
  display: inline-block;
  height: 20px;
  width: 20px;
  border-radius: 50%;
}

.status-column {
  text-align: center;
}

.green-status {
  background-color: lightgreen;
}

.red-status {
  background-color: red;
}

.dashboard-table tr {
  cursor: pointer;
}

.dashboard-table th:nth-child(1) {
  width: 10%; /* Adjust the width for the first column */
}

.dashboard-table th:nth-child(2) {
  width: 40%; /* Adjust the width for the second column */
}

.dashboard-table th:nth-child(3) {
  width: 40%; /* Adjust the width for the third column */
}

.dashboard-table th:nth-child(4) {
  width: 10%; /* Adjust the width for the fourth column */
}

/* === EMPLOYEES PAGE === */
/* Each page is a child of the "hero" element (see "MAIN PAGE" above) */

.employee-page {
  display: block;
  align-items: start;
}

.employees-container {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  background-color: var(--bg-color-secondary);
  border: 1px solid var(--accent-color-primary);
  border-radius: 10px;
  gap: 2rem;
  padding: 4rem 5rem 4rem 5rem;
  margin-top: 3rem;
}

.employees-container input {
  height: 2.5rem;
  width: 21rem;
  padding: 0.5rem;
  border: 2px solid var(--accent-color-primary);
  background-color: var(--accent-color-white);
  color: var(--bg-color-secondary);
  border-radius: 0.5rem;
}

.employees-container select {
  height: 2.5rem;
  width: 21rem;
  text-align: center;
  border: 2px solid var(--accent-color-primary);
  background-color: var(--accent-color-white);
  color: var(--bg-color-primary);
  border-radius: 0.5rem;
  padding: 0.2rem;
  transition: border-color 0.3s ease, background-color 0.3s ease;
}

.add-employees {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}

.remove-employees {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 2rem;
}

.add-employees button,
.remove-employees button {
  width: 10rem;
}






